@import "./variable";
@import "./mixin";
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,300&display=swap");
html {
    font-size: 62.5%;
    scroll-behavior: smooth;
}
body {
    font-family: "Roboto", sans-serif;
    font-size: 1.8rem;
    line-height: 1.5;
    text-rendering: optimizeSpeed;
    -webkit-font-smoothing: antialiased;
    width: 100vw;
    overflow-x: hidden !important;
    z-index: 0;
    background-color: $white;
}
select {
    cursor: pointer;
    font-size: 1.4rem;
    color: $mainBlr;
    * {
        cursor: pointer;
    }
}
* {
    padding: 0;
    margin: 0;
    text-decoration: none;
    color: unset;
    border: none;
    outline: none;
    background-color: unset;
    -webkit-tap-highlight-color: transparent;
    &:hover {
    }
}
button {
    cursor: pointer;
}
p {
    font-size: 1.4rem;
    color: $mainClr;
    line-height: 1.7;
}
/* width */
::-webkit-scrollbar {
    width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f15c;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: rgba(204, 204, 204, 0.416);
    border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: rgba(0, 81, 115, 0.594);
}
.admin {
    width: 100%;
    min-width: 50%;
    position: relative;
    cursor: text;
    z-index: 9;
    &::after {
        content: " ";
        width: 100%;
        height: 100%;
        border: 1px solid $mainClr;
        position: absolute;
        text-align: right;
        top: 0;
        border-radius: 3px;
        right: 0;
        color: $white;
        font-size: 1.2rem;
        opacity: 0;
        transition: 0.25s ease;
        font-weight: normal;
        pointer-events: none;
    }
    &::before {
        content: "🖊 Chỉnh sửa";
        position: absolute;
        -webkit-text-fill-color: $white;
        background-color: rgba(95, 95, 95, 0.4);
        top: 1px;
        right: 1px;
        padding: 2px 5px;
        border-radius: 3px;
        color: $white;
        font-size: 1.2rem;
        opacity: 0;
        z-index: 9;
        transition: 0.25s ease;
        font-weight: normal;
        pointer-events: none;
    }
    &:hover {
        &::after {
            opacity: 1;
        }
        &::before {
            opacity: 1;
        }
    }
}
.adminIMG {
    width: 100%;
    height: 100%;
    position: relative;
    cursor: grab;
    &::after {
        content: "";
        text-align: right;
        position: absolute;
        background-color: rgba(0, 27, 52, 0.24);
        width: 100%;
        height: 100%;
        @include flex(center);
        color: $white;
        top: 0;
        left: 0;
        z-index: 9;
        transition: 0.35s ease;
        opacity: 0;
        pointer-events: none;
    }
    &.adminVD {
        &::before {
            content: "🖊 Thay video";
        }
    }
    &::before {
        content: "🖊 Thay ảnh";
        position: absolute;
        background-color: rgba(204, 204, 204, 0.17);
        top: 50%;
        left: 50%;
        padding: 2px 5px;
        border-radius: 3px;
        transform: translate(-50%, -50%);
        color: $white;
        font-size: 1.2rem;
        opacity: 0;
        z-index: 99;
        transition: 0.25s ease;
        font-weight: normal;
        pointer-events: none;
    }
    &:hover {
        &::after {
            opacity: 1;
        }
        &::before {
            opacity: 1;
        }
    }
}
.opa0 {
    opacity: 0;
    position: absolute;
}

img {
    background: linear-gradient(rgba(204, 204, 204, 0.141), rgba(143, 143, 143, 0.128));
    width: 100%;
    height: 100%;
}
p,
h1 {
    word-wrap: break-word;
    width: 100%;
}
.switch {
    position: relative;
    cursor: pointer;
    width: 52px;
    height: 27px;
    display: block;
    margin: 0 auto;
    overflow: hidden;
    box-shadow: 0 0 10px 0 rgba(79, 79, 79, 0.1);
    background: #000;
    border-radius: 100rem;
    transition: background-color 0.25s linear;
}

.switch:after {
    content: "";
    position: absolute;
    width: 17px;
    height: 17px;
    border-radius: 40px;
    background-color: #fff;
    top: 5px;
    left: 7px;
    transition: background-color 0.25s linear, transform 0.25s linear;
}

.switch-input {
    display: none;
}

.switch-input:checked + .switch {
    background-color: $headText;
}

.switch-input:checked + .switch:after {
    transform: translateX(23px);
    background-color: white;
}
@media screen and (max-width: 768px) {
    h1 {
        font-size: 3rem !important;
    }
    h5 {
        font-size: 2.2rem !important;
    }
}
@media screen and (max-width: 768px) {
    .mnone {
        display: none !important;
    }
}
.btn {
    --body-bg: #ffff;
    --btn-gradient: linear-gradient(to right, #0558a0 0%, #00c3fe 100%);
    --btn-txt-color: #ffffff;
    position: relative;
    border: none;
    outline: none;
    display: block;
}

.btn-animation {
    border-radius: 5px;
}

.btn-animation:hover {
    cursor: pointer;
}

.btn-animation span {
    display: block;
}

.btn-animation span:nth-child(1) {
    padding: 2rem 6.5rem;
    background-image: var(--btn-gradient);
    border-radius: 5px;
}

.btn-animation span:nth-child(2),
.btn-animation span:nth-child(3),
.btn-animation span:nth-child(4) {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
svg {
    pointer-events: none !important;
}
.btn-animation span:nth-child(4) {
    width: calc(100% - 16px);
    height: calc(100% - 16px);
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: var(--btn-gradient);
    color: var(--btn-txt-color);
    font-weight: 600;
    font-size: 1.5rem;
    transition: all 0.3s ease;
    border-radius: 1px;
}

.btn-animation:hover span:nth-child(4) {
    width: 100%;
    height: 100%;
    border-radius: 5px;
}

.btn-animation span:nth-child(3) {
    width: calc(100% - 4px);
    height: calc(100% - 4px);
    background-color: var(--body-bg);
    border-radius: 4px;
}

.btn-animation span:nth-child(2) {
    height: 60%;
    width: 100%;
    background-color: var(--body-bg);
    animation: rotate 2s linear infinite;
}

@keyframes rotate {
    0% {
        transform: translate(-50%, -50%) rotate(0deg);
    }

    100% {
        transform: translate(-50%, -50%) rotate(180deg);
    }
}
.input-focus-effect {
    position: relative;
    display: flex;
    flex-direction: column-reverse;
    width: 95% !important;
    box-sizing: border-box;
}

.input-focus-effect input {
    padding: 25px 10px 10px;
    border: 1px solid transparent;
    display: block;
    width: 100%;
    border-radius: 5px;
    transition: border 0.25s linear;
    background-color: transparent;
    outline: none;
    color: $mainClr !important;
    font-size: 15px;
}

.input-focus-effect input:focus {
    border-color: $mainClr;
}

.input-focus-effect input:focus + label,
.input-focus-effect input:not(:placeholder-shown) + label {
    transform: translateY(-5px);
    top: 10px;
}

.input-focus-effect label {
    position: absolute;
    top: 50%;
    left: 10px;
    transform: translateY(-50%);
    pointer-events: none;
    color: $mainClr;
    transition: 0.25s linear;
    font-size: 14px;
}
label {
    user-select: none;
}
